import React from 'react';
import reactStringReplace from 'react-string-replace'

type Props = {
  children: React.ReactNode,
};

export function HighlightedText(props: Props) {
  let output = [props.children];
  output = reactStringReplace(output, /[â–²]/g, (match, i) => (
    <><span className="triangle">â–²</span>{match}</>
  ))
  output = reactStringReplace(output, /[â—â—‹â¬¤]/g, (match, i) => (
    <><span className="circle">â—</span>{match}</>
  ))
  output = reactStringReplace(output, /[â¬›â– ]/g, (match, i) => (
    <><span className="square">â– </span>{match}</>
  ))
  output = reactStringReplace(output, /[â¬Ÿ]/g, (match, i) => (
    <><span className="pentagon">â¬Ÿ</span>{match}</>
  ))
  return <>{output}</>
}
